.sidebar {
    height: 100vh;
    width: 16%;
    padding: 2%;
    background-color: #3e7f91;
    color: #fff;
    margin-left: 2%;
    margin-top: 2%;
    margin-bottom: 2%;
}
.sidebar ul {
    list-style-type: none; /* Remove default list styles */
    padding: 10%; /* Remove padding */
    background-color: #fff;
    color: #3e7f91;
}
.sidebar ul li {
    position: relative; /* Position relative to allow absolute positioning of hover effect */
    margin: 0px 0px 10px 0px; /* Margin between list items */
}
.sidebar ul li a {
    display: block;
    padding: 5%; /* Padding for the link */
    text-decoration: none; /* Remove underline from links */
    color: #3e7f91; /* Text color */
    font-weight: bold; /* Bold text */
    position: relative;
    z-index: 2; /* Ensure the text stays on top */
}
.sidebar ul li:hover::before {
    content: "";
    position: absolute;
    width: 100%; /* Fill the entire li */
    height: 100%; /* Fill the entire li */ 
    background-color: #ececec; /* Light gray background on hover */
}