.app-container {
  display: flex;
  flex-direction: column;
}

.app-header {
  background-color: #3e7f91; /* Blue background */
  color: white; /* White text */
  text-align: center; /* Center text */
}

.app-body {
  display: flex; /* Flexbox for sidebar and content */
}

.main-content {
  flex: 1; /* Allow the main content to take full width */
  padding: 2%; 
}